<template>
  <div class="adsense-container" :class="containerClass" v-if="showAd">
    <ins class="adsbygoogle"
         :style="adStyle"
         :data-ad-client="adClient"
         :data-ad-slot="adSlot"
         :data-ad-format="adFormat"
         :data-full-width-responsive="fullWidthResponsive"></ins>
  </div>
</template>

<script>
export default {
  name: 'AdSenseAd',
  props: {
    adSlot: {
      type: String,
      required: true
    },
    adFormat: {
      type: String,
      default: 'auto'
    },
    fullWidthResponsive: {
      type: String,
      default: 'true'
    },
    width: {
      type: [String, Number],
      default: null
    },
    height: {
      type: [String, Number],
      default: null
    },
    containerClass: {
      type: String,
      default: ''
    },
    // ÊéßÂà∂ÊòØÂê¶ÊòæÁ§∫ÂπøÂëäÔºàÁî®‰∫éÂºÄÂèëÁéØÂ¢ÉÔºâ
    enabled: {
      type: Boolean,
      default: () => process.env.VUE_APP_ADSENSE_ENABLED === 'true'
    },
    // Âº∫Âà∂ÈöêËóèÂπøÂëäÔºàÁî®‰∫éÈîôËØØÈ°µÈù¢„ÄÅ404È°µÈù¢Á≠âÔºâ
    forceHide: {
      type: Boolean,
      default: false
    },
    // Ë¶ÅÊ±ÇÈ°µÈù¢ÊúâÂÆûË¥®ÂÜÖÂÆπÊâçÊòæÁ§∫ÂπøÂëä
    requireContent: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      adClient: process.env.VUE_APP_ADSENSE_CLIENT || 'ca-pub-8250499267865326',
      showAd: false
    }
  },
  computed: {
    adStyle() {
      const style = {
        display: 'block'
      }
      if (this.width) {
        style.width = typeof this.width === 'number' ? `${this.width}px` : this.width
      }
      if (this.height) {
        style.height = typeof this.height === 'number' ? `${this.height}px` : this.height
      }
      return style
    }
  },
  mounted() {
    // Ê£ÄÊü•ÊòØÂê¶Â∫îËØ•ÊòæÁ§∫ÂπøÂëä
    this.checkAdDisplay()
    
    if (this.showAd) {
      this.loadAd()
    }
  },
  methods: {
    checkAdDisplay() {
      // Âº∫Âà∂ÈöêËóèÂπøÂëä
      if (this.forceHide) {
        this.showAd = false
        console.log('AdSense ad blocked: forceHide prop is true')
        return
      }
      
      // Âú®ÂºÄÂèëÁéØÂ¢É‰∏≠ÂèØ‰ª•ÈÄâÊã©‰∏çÊòæÁ§∫ÂπøÂëä
      if (process.env.NODE_ENV === 'development' && !this.enabled) {
        this.showAd = false
        return
      }
      
      // Ê£ÄÊü•È°µÈù¢ÊòØÂê¶ÂåÖÂê´ÂÆûË¥®ÂÜÖÂÆπÔºàGoogle AdSense ÊîøÁ≠ñË¶ÅÊ±ÇÔºâ
      if (this.requireContent && !this.hasSubstantialContent()) {
        this.showAd = false
        console.log('AdSense ad blocked: page lacks substantial content')
        return
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊúâAdSenseËÑöÊú¨
      if (typeof window !== 'undefined' && window.adsbygoogle) {
        this.showAd = true
      } else {
        // Á≠âÂæÖAdSenseËÑöÊú¨Âä†ËΩΩ
        this.waitForAdSense()
      }
    },
    
    hasSubstantialContent() {
      // È¶ñÂÖàÊ£ÄÊü•Ë∑ØÁî±ÂÖÉ‰ø°ÊÅØÊòØÂê¶ÂÖÅËÆ∏ÂπøÂëä
      const currentRoute = this.$route
      if (currentRoute.meta && currentRoute.meta.allowAds === false) {
        console.log(`AdSense ad blocked: route '${currentRoute.name}' has allowAds: false in meta`)
        return false
      }
      
      // Ê£ÄÊü•ÂΩìÂâçË∑ØÁî±ÊòØÂê¶‰∏∫ÂÜÖÂÆπÈ°µÈù¢
      const currentRouteName = currentRoute.name
      const contentRoutes = ['bazi-results', 'astrology-results', 'transit-analysis', 'bazi-results-shared', 'astrology-results-shared']
      
      // ÊòéÁ°ÆÁ¶ÅÊ≠¢Âú®Ëøô‰∫õÈ°µÈù¢ÊòæÁ§∫ÂπøÂëä - Êâ©Â±ïÁ¶ÅÊ≠¢ÂàóË°®Ôºà‰Ωú‰∏∫Â§áÁî®Ê£ÄÊü•Ôºâ
      const prohibitedRoutes = [
        'error', 'not-found', '404', 
        'home', 'HomePage',  // È¶ñÈ°µÔºàË°®ÂçïÈ°µÈù¢Ôºâ
        'loading', 'maintenance',  // Âä†ËΩΩÂíåÁª¥Êä§È°µÈù¢
        'terms', 'privacy',  // Ê≥ïÂæãÊù°Ê¨æÈ°µÈù¢
        'contact', 'about'   // ÈùôÊÄÅ‰ø°ÊÅØÈ°µÈù¢
      ]
      if (prohibitedRoutes.includes(currentRouteName)) {
        console.log(`AdSense ad blocked: route '${currentRouteName}' is in prohibited list`)
        return false
      }
      
      // Âè™ÂÖÅËÆ∏Âú®ÁªìÊûúÈ°µÈù¢ÊòæÁ§∫ÂπøÂëä
      if (!contentRoutes.includes(currentRouteName)) {
        console.log(`AdSense ad blocked: route '${currentRouteName}' is not a content route`)
        return false
      }
      
      // ÂØπ‰∫éÁªìÊûúÈ°µÈù¢ÔºåÊ£ÄÊü•ÊòØÂê¶ÊúâÂÆûÈôÖËÆ°ÁÆóÁªìÊûú
      if (['bazi-results', 'astrology-results', 'transit-analysis', 'bazi-results-shared', 'astrology-results-shared'].includes(currentRouteName)) {
        const hasResults = this.$store.getters.getCalculationResults || this.$store.getters.getUserData
        if (!hasResults) {
          console.log('AdSense ad blocked: no calculation results found')
          return false
        }
        
        // È¢ùÂ§ñÊ£ÄÊü•ÔºöÁ°Æ‰øùËÆ°ÁÆóÁªìÊûú‰∏ç‰∏∫Á©∫ÊàñÊó†Êïà
        const results = this.$store.getters.getCalculationResults
        if (results && typeof results === 'object') {
          // Ê£ÄÊü•ÁªìÊûúÊòØÂê¶ÂåÖÂê´ÂÆûË¥®ÂÜÖÂÆπ
          const hasValidResults = results.eightCharacters || results.astrology || results.analysis
          if (!hasValidResults) {
            console.log('AdSense ad blocked: calculation results are empty')
            return false
          }
        }
      }
      
      // Ê£ÄÊü•È°µÈù¢ÊòØÂê¶ÊúâË∂≥Â§üÁöÑÊñáÊú¨ÂÜÖÂÆπ
      if (typeof document !== 'undefined') {
        const bodyText = document.body.innerText || document.body.textContent || ''
        const minContentLength = 1500 // ÊèêÈ´òÂà∞1500Â≠óÁ¨¶Á°Æ‰øùÊúâË∂≥Â§üÂÜÖÂÆπ
        
        if (bodyText.length < minContentLength) {
          console.log(`AdSense ad blocked: content too short (${bodyText.length} chars, min: ${minContentLength})`)
          return false
        }
        
        // Ê£ÄÊü•ÊòØÂê¶ÂåÖÂê´ÈîôËØØÈ°µÈù¢„ÄÅÂØºËà™È°µÈù¢„ÄÅË°®ÂçïÈ°µÈù¢Á≠âÈùûÂÆûË¥®ÂÜÖÂÆπ
        const hasProhibitedContent = bodyText.includes('„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü') || 
                              bodyText.includes('„Éö„Éº„Ç∏„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì') ||
                              bodyText.includes('404') ||
                              bodyText.includes('ÂïèÈ°å„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü') ||
                              bodyText.includes('Êòü„ÄÖ„ÅÆ‰∏≠„Åß„Éö„Éº„Ç∏„ÅåËø∑Â≠ê') ||
                              bodyText.includes('Ëß£Ê±∫ÊñπÊ≥ï') ||
                              bodyText.includes('„Éá„Éê„ÉÉ„Ç∞ÊÉÖÂ†±') ||
                              // Ê∑ªÂä†Ë°®ÂçïÈ°µÈù¢Áõ∏ÂÖ≥ÂÜÖÂÆπÊ£ÄÊµã
                              bodyText.includes('ÁîüÂπ¥ÊúàÊó•') ||
                              bodyText.includes('Âá∫ÁîüÊó∂Èó¥') ||
                              bodyText.includes('Âá∫ÁîüÂú∞') ||
                              bodyText.includes('ÂàÜÊûêÁ±ªÂûãÈÄâÊã©') ||
                              bodyText.includes('ÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ') ||
                              // Ê∑ªÂä†ÂØºËà™ÂíåÂª∫ËÆæ‰∏≠È°µÈù¢Ê£ÄÊµã
                              bodyText.includes('Â∑•‰∫ã‰∏≠') ||
                              bodyText.includes('Ê∫ñÂÇô‰∏≠') ||
                              bodyText.includes('„É°„É≥„ÉÜ„Éä„É≥„Çπ') ||
                              bodyText.includes('„Åó„Å∞„Çâ„Åè„ÅäÂæÖ„Å°')
        
        if (hasProhibitedContent) {
          console.log('AdSense ad blocked: page contains prohibited content (forms, errors, navigation, etc.)')
          return false
        }
        
        // Ê£ÄÊü•È°µÈù¢URLË∑ØÂæÑ
        const currentPath = this.$route.path
        const prohibitedPaths = ['error', '404', 'not-found', 'home', 'index', 'contact', 'about', 'terms', 'privacy']
        if (prohibitedPaths.some(path => currentPath.includes(path))) {
          console.log(`AdSense ad blocked: path '${currentPath}' contains prohibited segments`)
          return false
        }
      }
      
      return true
    },
    
    waitForAdSense() {
      let attempts = 0
      const maxAttempts = 10
      
      const checkInterval = setInterval(() => {
        attempts++
        if (window.adsbygoogle) {
          this.showAd = true
          this.loadAd()
          clearInterval(checkInterval)
        } else if (attempts >= maxAttempts) {
          console.warn('AdSense script not loaded after maximum attempts')
          clearInterval(checkInterval)
        }
      }, 500)
    },
    
    loadAd() {
      this.$nextTick(() => {
        try {
          if (window.adsbygoogle && this.$el.querySelector('.adsbygoogle')) {
            (window.adsbygoogle = window.adsbygoogle || []).push({})
          }
        } catch (error) {
          console.error('Failed to load AdSense ad:', error)
        }
      })
    }
  }
}
</script>

<style scoped>
.adsense-container {
  margin: 20px 0;
  text-align: center;
}

.adsense-container.banner {
  max-width: 728px;
  margin: 20px auto;
}

.adsense-container.rectangle {
  max-width: 300px;
  margin: 20px auto;
}

.adsense-container.responsive {
  width: 100%;
  max-width: 100%;
}

/* ÁßªÂä®Á´ØÈÄÇÈÖç */
@media (max-width: 768px) {
  .adsense-container {
    margin: 15px 0;
  }
  
  .adsense-container.banner {
    max-width: 320px;
  }
}
</style>