/**
 * ÂæÆ‰ø°ÂàÜ‰∫´ÈÖçÁΩÆÂ∑•ÂÖ∑
 * ÈõÜÊàêÂæÆ‰ø°JS-SDKÂÆûÁé∞ÂéüÁîüÂàÜ‰∫´‰ΩìÈ™å
 */

/**
 * Ê£ÄÊü•ÊòØÂê¶Âú®ÂæÆ‰ø°ÊµèËßàÂô®‰∏≠
 * @returns {boolean}
 */
export function isWechatBrowser() {
  return /micromessenger/i.test(navigator.userAgent);
}

/**
 * Âä®ÊÄÅÂä†ËΩΩÂæÆ‰ø°JS-SDK
 * @returns {Promise<boolean>} Âä†ËΩΩÊàêÂäüËøîÂõûtrue
 */
export function loadWechatJSSDK() {
  return new Promise((resolve, reject) => {
    // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÂä†ËΩΩ
    if (window.wx) {
      resolve(true);
      return;
    }

    // ÂàõÂª∫scriptÊ†áÁ≠æ
    const script = document.createElement('script');
    script.src = 'https://res.wx.qq.com/open/js/jweixin-1.6.0.js';
    script.onload = () => resolve(true);
    script.onerror = () => reject(new Error('ÂæÆ‰ø°JS-SDKÂä†ËΩΩÂ§±Ë¥•'));
    
    document.head.appendChild(script);
  });
}

/**
 * ÈÖçÁΩÆÂæÆ‰ø°ÂàÜ‰∫´ÂèÇÊï∞
 * @param {Object} config - ÂàÜ‰∫´ÈÖçÁΩÆ
 * @param {string} config.title - ÂàÜ‰∫´Ê†áÈ¢ò
 * @param {string} config.desc - ÂàÜ‰∫´ÊèèËø∞  
 * @param {string} config.link - ÂàÜ‰∫´ÈìæÊé•
 * @param {string} config.imgUrl - ÂàÜ‰∫´ÂõæÁâáURL
 * @param {Function} config.success - ÂàÜ‰∫´ÊàêÂäüÂõûË∞É
 * @param {Function} config.cancel - ÂàÜ‰∫´ÂèñÊ∂àÂõûË∞É
 */
export async function configWechatShare(config) {
  try {
    // Ê£ÄÊü•ÂæÆ‰ø°ÁéØÂ¢É
    if (!isWechatBrowser()) {
      console.log('ÈùûÂæÆ‰ø°ÁéØÂ¢ÉÔºåË∑≥ËøáÂæÆ‰ø°ÂàÜ‰∫´ÈÖçÁΩÆ');
      return false;
    }

    // Âä†ËΩΩÂæÆ‰ø°JS-SDK
    await loadWechatJSSDK();

    // ËøôÈáåÈúÄË¶ÅÂêéÁ´ØÊèê‰æõÁ≠æÂêç‰ø°ÊÅØ
    // ÊöÇÊó∂‰ΩøÁî®Ê®°ÊãüÈÖçÁΩÆÔºåÂÆûÈôÖ‰ΩøÁî®Êó∂ÈúÄË¶ÅË∞ÉÁî®ÂêéÁ´ØAPIËé∑ÂèñÁ≠æÂêç
    const wxConfig = {
      debug: false, // Áîü‰∫ßÁéØÂ¢ÉËÆæ‰∏∫false
      appId: 'your_app_id', // ÈúÄË¶ÅÈÖçÁΩÆ
      timestamp: Math.floor(Date.now() / 1000),
      nonceStr: generateNonceStr(),
      signature: 'your_signature', // ÈúÄË¶ÅÂêéÁ´ØÊèê‰æõ
      jsApiList: [
        'onMenuShareTimeline',
        'onMenuShareAppMessage', 
        'onMenuShareQQ',
        'onMenuShareQZone',
        'updateAppMessageShareData',
        'updateTimelineShareData'
      ]
    };

    // ÈÖçÁΩÆÂæÆ‰ø°JS-SDK
    window.wx.config(wxConfig);

    // ÂæÆ‰ø°ÈÖçÁΩÆÊàêÂäüÂêéËÆæÁΩÆÂàÜ‰∫´ÂèÇÊï∞
    window.wx.ready(() => {
      // Êñ∞ÁâàAPIÔºöÂàÜ‰∫´ÁªôÊúãÂèã
      window.wx.updateAppMessageShareData({
        title: config.title,
        desc: config.desc,
        link: config.link,
        imgUrl: config.imgUrl,
        success: config.success || (() => console.log('ÂàÜ‰∫´ÁªôÊúãÂèãÊàêÂäü')),
        cancel: config.cancel || (() => console.log('ÂèñÊ∂àÂàÜ‰∫´ÁªôÊúãÂèã'))
      });

      // Êñ∞ÁâàAPIÔºöÂàÜ‰∫´Âà∞ÊúãÂèãÂúà
      window.wx.updateTimelineShareData({
        title: config.title,
        link: config.link,
        imgUrl: config.imgUrl,
        success: config.success || (() => console.log('ÂàÜ‰∫´Âà∞ÊúãÂèãÂúàÊàêÂäü')),
        cancel: config.cancel || (() => console.log('ÂèñÊ∂àÂàÜ‰∫´Âà∞ÊúãÂèãÂúà'))
      });

      // ÂÖºÂÆπÊóßÁâàAPI
      window.wx.onMenuShareAppMessage({
        title: config.title,
        desc: config.desc,
        link: config.link,
        imgUrl: config.imgUrl,
        success: config.success,
        cancel: config.cancel
      });

      window.wx.onMenuShareTimeline({
        title: config.title,
        link: config.link,
        imgUrl: config.imgUrl,
        success: config.success,
        cancel: config.cancel
      });

      console.log('ÂæÆ‰ø°ÂàÜ‰∫´ÈÖçÁΩÆÊàêÂäü');
    });

    // ÂæÆ‰ø°ÈÖçÁΩÆÂ§±Ë¥•Â§ÑÁêÜ
    window.wx.error((res) => {
      console.error('ÂæÆ‰ø°JS-SDKÈÖçÁΩÆÂ§±Ë¥•:', res);
    });

    return true;
  } catch (error) {
    console.error('ÈÖçÁΩÆÂæÆ‰ø°ÂàÜ‰∫´Â§±Ë¥•:', error);
    return false;
  }
}

/**
 * ÁîüÊàêÈöèÊú∫Â≠óÁ¨¶‰∏≤ÔºàÁî®‰∫éÂæÆ‰ø°Á≠æÂêçÔºâ
 * @returns {string}
 */
function generateNonceStr() {
  return Math.random().toString(36).slice(2, 15) + 
         Math.random().toString(36).slice(2, 15);
}

