/**
 * Ëá™ÂÆö‰πâÂàÜ‰∫´Ëß¶ÂèëÔºàÂú®ÈùûÂæÆ‰ø°ÁéØÂ¢É‰ΩøÁî®Ôºâ
 * @param {Object} shareData - ÂàÜ‰∫´Êï∞ÊçÆ
 */
export function triggerCustomShare(shareData) {
  if (navigator.share) {
    // ‰ΩøÁî®Web Share API
    navigator.share({
      title: shareData.title,
      text: shareData.desc,
      url: shareData.link
    }).then(() => {
      console.log('Web Share APIÂàÜ‰∫´ÊàêÂäü');
    }).catch((error) => {
      console.log('Web Share APIÂàÜ‰∫´Â§±Ë¥•:', error);
      fallbackShare(shareData);
    });
  } else {
    fallbackShare(shareData);
  }
}

/**
 * ÈôçÁ∫ßÂàÜ‰∫´ÊñπÊ°à
 * @param {Object} shareData - ÂàÜ‰∫´Êï∞ÊçÆ
 */
function fallbackShare(shareData) {
  // Â§çÂà∂ÈìæÊé•Âà∞Ââ™Ë¥¥Êùø
  if (navigator.clipboard && navigator.clipboard.writeText) {
    navigator.clipboard.writeText(shareData.link).then(() => {
      alert('ÈìæÊé•Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÔºåËØ∑ÊâãÂä®ÂàÜ‰∫´');
    }).catch(() => {
      showShareModal(shareData);
    });
  } else {
    showShareModal(shareData);
  }
}

/**
 * ÊòæÁ§∫ÂàÜ‰∫´Ê®°ÊÄÅÊ°Ü
 * @param {Object} shareData - ÂàÜ‰∫´Êï∞ÊçÆ
 */
function showShareModal(shareData) {
  // ÂàõÂª∫ÂàÜ‰∫´Ê®°ÊÄÅÊ°Ü
  const modal = document.createElement('div');
  modal.style.cssText = `
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10000;
  `;

  const content = document.createElement('div');
  content.style.cssText = `
    background: white;
    padding: 20px;
    border-radius: 10px;
    max-width: 300px;
    text-align: center;
  `;

  content.innerHTML = `
    <h3>${shareData.title}</h3>
    <p>${shareData.desc}</p>
    <input type="text" value="${shareData.link}" style="width: 100%; padding: 10px; margin: 10px 0;" readonly>
    <div>
      <button onclick="navigator.clipboard && navigator.clipboard.writeText('${shareData.link}').then(() => alert('Â∑≤Â§çÂà∂'))" 
              style="margin: 5px; padding: 10px 20px;">Â§çÂà∂ÈìæÊé•</button>
      <button onclick="this.closest('[style*=fixed]').remove()" 
              style="margin: 5px; padding: 10px 20px;">ÂÖ≥Èó≠</button>
    </div>
  `;

  modal.appendChild(content);
  document.body.appendChild(modal);

  // ÁÇπÂáªËÉåÊôØÂÖ≥Èó≠
  modal.addEventListener('click', (e) => {
    if (e.target === modal) {
      modal.remove();
    }
  });
}

/**
 * ÁîüÊàêÂàÜ‰∫´ÁöÑOpen Graph metaÊ†áÁ≠æ
 * @param {Object} shareData - ÂàÜ‰∫´Êï∞ÊçÆ
 */
export function generateOpenGraphMeta(shareData) {
  const metaTags = [
    { property: 'og:title', content: shareData.title },
    { property: 'og:description', content: shareData.desc },
    { property: 'og:image', content: shareData.imgUrl },
    { property: 'og:url', content: shareData.link },
    { property: 'og:type', content: 'website' },
    { name: 'twitter:card', content: 'summary_large_image' },
    { name: 'twitter:title', content: shareData.title },
    { name: 'twitter:description', content: shareData.desc },
    { name: 'twitter:image', content: shareData.imgUrl }
  ];

  // ÁßªÈô§Áé∞ÊúâÁöÑmetaÊ†áÁ≠æ
  metaTags.forEach(tag => {
    const selector = tag.property ? `meta[property="${tag.property}"]` : `meta[name="${tag.name}"]`;
    const existing = document.querySelector(selector);
    if (existing) {
      existing.remove();
    }
  });

  // Ê∑ªÂä†Êñ∞ÁöÑmetaÊ†áÁ≠æ
  metaTags.forEach(tag => {
    const meta = document.createElement('meta');
    if (tag.property) {
      meta.setAttribute('property', tag.property);
    } else {
      meta.setAttribute('name', tag.name);
    }
    meta.setAttribute('content', tag.content);
    document.head.appendChild(meta);
  });
}

/**
 * ÁªüËÆ°ÂàÜ‰∫´‰∫ã‰ª∂ÔºàÂ¶ÇÊûúÊúâÁªüËÆ°ÈúÄÊ±ÇÔºâ
 * @param {string} platform - ÂàÜ‰∫´Âπ≥Âè∞
 * @param {string} type - ÂàÜ‰∫´Á±ªÂûã
 */
export function trackShareEvent(platform, type) {
  // ËøôÈáåÂèØ‰ª•ÈõÜÊàêÁªüËÆ°ÂàÜÊûêÂ∑•ÂÖ∑
  // ÊØîÂ¶Ç Google Analytics, ÁôæÂ∫¶ÁªüËÆ°Á≠â
  
  if (window.gtag) {
    window.gtag('event', 'share', {
      method: platform,
      content_type: type,
      content_id: window.location.pathname
    });
  }

  console.log(`ÂàÜ‰∫´ÁªüËÆ°: ${platform} - ${type}`);
}